<template>
  <div>
    <div class="title mb-2">t li mt khu</div>
    <div class="form-group">
        <label for="exampleInputEmail1">Email</label>
        <input
          v-validate="'required|email'"
          v-model="email"
          name="email"
          type="text"
          data-vv-as="a ch email"
          :class="['form-control', this.errors.first('email') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('email') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">M token</label>
        <input
          v-validate="'required'"
          v-model="token"
          name="token"
          type="text"
          data-vv-as="M token"
          :class="['form-control', this.errors.first('token') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('token') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">Mt khu mi</label>
        <input
          v-validate="'required|min:8'"
          v-model="pw"
          name="pw"
          type="password"
          data-vv-as="Mt khu mi"
          :class="['form-control', this.errors.first('pw') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('pw') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">Nhp li mt khu mi</label>
        <input
          v-validate="'required|min:8'"
          v-model="retype_pw"
          name="retype_pw"
          type="password"
          data-vv-as="Nhp li mt khu mi"
          :class="['form-control', this.errors.first('retype_pw') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('retype_pw') }}</div>
    </div>
    <div class="d-flex form-group">
      <button class="btn btn-primary ml-auto" @click="resetPw">
        <span class="icon-forget-pw d-inline mr-2"></span>
        Cp nht mt khu mi
      </button>
    </div>

  </div>
</template>

<script>
import request from 'superagent'
import appHelper from '../helper/helper.js'

export default {
  data () {
    return {
      email: '',
      token: '',
      pw: '',
      retype_pw: ''
    }
  },

  methods: {
    resetPw () {
      this.$validator.validate().then(result => {
        if (result) {
          if (this.pw !== this.retype_pw) {
            this.errors.add('pw', 'Mt khu khng bng mt khu nhp li mt khu')
            return
          }

          // Kim nhp li mt khu
          request.post('http://deltavn.net/api/password/reset').send({
            token: this.token,
            password: this.pw,
            password_confirmation: this.retype_pw,
            email: this.email
          }).then((res) => {
            if (res.body.error) {
              appHelper.taoModalBaoLoi(res.body.error, this, 'Cp nht li mt khu tht bi')
              return
            }
            appHelper.taoModalThongBao('t li mt khu thnh cng', this)
            this.$router.replace('/reset_pw')
          })
        }
      })
    }
  }
}
</script>

<style>

</style>



