export default {
  taoModalThongBao (NoiDung, _this) {
    _this.$modal.show('dialog', {
      title: 'Thng bo',
      text: NoiDung,
      buttons: [
        {
          title: 'ng',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  },

  taoModalBaoLoi (NoiDung, _this, tit) {
    _this.$modal.show('dialog', {
      title: tit || ' xy ra li',
      text: NoiDung,
      buttons: [
        {
          title: 'ng',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  },

  datCauHoi (cauHoi, traLoiCo, _this) {
    _this.$modal.show('dialog', {
      title: 'Xc nhn',
      text: cauHoi,
      buttons: [
        {
          title: 'C',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
            traLoiCo()
          }
        }, {
          title: 'Khng',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  }
}



